import axios from 'axios'
export default {
  name: 'BindForm',
  data () {
    return {
      tag: false,
      item: {
        name: '',
        telephone: '',
        level: Number,
        code: this.$global.getUrlKey('code')
      },
      level: [
        {
          value: 6,
          label: '最高负责人'
        },
        {
          value: 4,
          label: '老师'
        },
        {
          value: 5,
          label: '助理'
        },
        {
          value: 3,
          label: '学生'
        },
        {
          value: 2,
          label: '专家教授'
        }
      ]
    }
  },
  methods: {
    submit () {
      if (this.item.name === '' || this.item.telephone === '') {
        this.$notify.warning({
          title: '请输入姓名和电话!'
        })
        return
      }
      this.$axios({
        method: 'put',
        url: this.url + '/user/bind',
        data: JSON.stringify(this.item)
      }).then((user) => {
        if (user.status === 200) {
          alert('绑定成功')
          const data = user.data
          localStorage.setItem('level', data.level)
          localStorage.setItem('id', data.id)
          localStorage.setItem('name', data.name)
          this.$router.push('/project')
        } else {
          alert(user)
        }
      }).catch((err) => {
        alert(err)
      })
    },
    getInfo () {
      axios.get(this.url + '/user/id/' + localStorage.getItem('id'))
        .then(this.getInfoSucc)
    },
    getInfoSucc (res) {
      if (res.status !== 200) alert(res)
      else if (res) {
        res = res.data
        this.item.name = res.name
        this.item.telephone = res.telephone
      }
    },
    off () {
      localStorage.setItem('id', '')
      localStorage.setItem('level', '')
      localStorage.setItem('name', '')
      alert('off')
    }
  },
  created () {
    this.item.level = 1
    if (localStorage.getItem('id')) {
      this.getInfo()
      this.tag = true
    }
  }
}



